import remindersService from './reminders';
import tasksService from './tasks';
import projectsService from './projects';
import dayjs from 'dayjs';

/**
 * Fetch all calendar items (reminders + tasks) for a user
 * @param {string} userId - User ID
 * @param {string} date - Optional date filter (YYYY-MM-DD format)
 * @returns {Promise<Array>} Array of calendar items with type, date, and metadata
 */
export async function getCalendarItems(userId, date = null) {
  try {
    const items = [];

    // Fetch reminders
    try {
      const reminders = await remindersService.listReminders(userId, date);
      reminders.forEach(reminder => {
        items.push({
          type: 'reminder',
          id: reminder.$id,
          title: reminder.title,
          description: reminder.description,
          date: reminder.date,
          time: dayjs(reminder.date).format('HH:mm'),
          priority: reminder.priority || 'medium',
          notification: reminder.notification,
          assignedTo: reminder.assignedTo || [],
          recurring: reminder.recurring,
          ownerId: reminder.ownerId,
          source: 'reminder'
        });
      });
    } catch (err) {
      console.warn('[CalendarData] Error fetching reminders:', err);
    }

    // Fetch tasks from user's projects
    try {
      const projects = await projectsService.listProjects(userId);
      const projectIds = projects.map(p => p.$id);

      for (const projectId of projectIds) {
        try {
          const tasks = await tasksService.listTasks(projectId);
          tasks.forEach(task => {
            if (task.dueDate) {
              const taskDate = dayjs(task.dueDate).format('YYYY-MM-DD');
              
              // If date filter is provided, only include matching tasks
              if (date && taskDate !== date) {
                return;
              }

              items.push({
                type: 'task',
                id: task.$id,
                title: task.title,
                description: task.description,
                date: task.dueDate,
                time: dayjs(task.dueDate).format('HH:mm'),
                priority: task.priority || 'Medium',
                status: task.status,
                projectId: task.projectId,
                assignedTo: [], // Tasks don't have assignedTo yet, but structure is ready
                recurring: null,
                ownerId: task.createdBy,
                source: 'task',
                isOverdue: new Date(task.dueDate) < new Date() && task.status !== 'Completed'
              });
            }
          });
        } catch (err) {
          console.warn(`[CalendarData] Error fetching tasks for project ${projectId}:`, err);
        }
      }
    } catch (err) {
      console.warn('[CalendarData] Error fetching projects:', err);
    }

    // Sort by date/time
    items.sort((a, b) => {
      const dateA = new Date(a.date);
      const dateB = new Date(b.date);
      return dateA - dateB;
    });

    return items;
  } catch (err) {
    console.error('[CalendarData] Error getting calendar items:', err);
    return [];
  }
}

/**
 * Get calendar items for a specific date
 * @param {string} userId - User ID
 * @param {string} date - Date in YYYY-MM-DD format
 * @returns {Promise<Array>} Array of items for that date
 */
export async function getDayItems(userId, date) {
  return getCalendarItems(userId, date);
}

/**
 * Get upcoming items (next N days)
 * @param {string} userId - User ID
 * @param {number} days - Number of days ahead to fetch (default: 7)
 * @returns {Promise<Array>} Array of upcoming items
 */
export async function getUpcomingItems(userId, days = 7) {
  try {
    const allItems = await getCalendarItems(userId);
    const today = dayjs().startOf('day');
    const endDate = today.add(days, 'day');

    return allItems.filter(item => {
      const itemDate = dayjs(item.date).startOf('day');
      return itemDate.isAfter(today.subtract(1, 'day')) && itemDate.isBefore(endDate.add(1, 'day'));
    });
  } catch (err) {
    console.error('[CalendarData] Error getting upcoming items:', err);
    return [];
  }
}

/**
 * Get overdue items
 * @param {string} userId - User ID
 * @returns {Promise<Array>} Array of overdue items
 */
export async function getOverdueItems(userId) {
  try {
    const allItems = await getCalendarItems(userId);
    const today = dayjs().startOf('day');

    return allItems.filter(item => {
      const itemDate = dayjs(item.date).startOf('day');
      const isOverdue = itemDate.isBefore(today);
      const isCompleted = item.type === 'task' && item.status === 'Completed';
      return isOverdue && !isCompleted;
    });
  } catch (err) {
    console.error('[CalendarData] Error getting overdue items:', err);
    return [];
  }
}

/**
 * Get items grouped by date
 * @param {string} userId - User ID
 * @param {string} startDate - Start date (YYYY-MM-DD)
 * @param {string} endDate - End date (YYYY-MM-DD)
 * @returns {Promise<Object>} Object with dates as keys and arrays of items as values
 */
export async function getItemsByDateRange(userId, startDate, endDate) {
  try {
    const allItems = await getCalendarItems(userId);
    const start = dayjs(startDate).startOf('day');
    const end = dayjs(endDate).endOf('day');

    const filtered = allItems.filter(item => {
      const itemDate = dayjs(item.date).startOf('day');
      return itemDate.isAfter(start.subtract(1, 'day')) && itemDate.isBefore(end.add(1, 'day'));
    });

    // Group by date
    const grouped = {};
    filtered.forEach(item => {
      const dateKey = dayjs(item.date).format('YYYY-MM-DD');
      if (!grouped[dateKey]) {
        grouped[dateKey] = [];
      }
      grouped[dateKey].push(item);
    });

    return grouped;
  } catch (err) {
    console.error('[CalendarData] Error getting items by date range:', err);
    return {};
  }
}

export default {
  getCalendarItems,
  getDayItems,
  getUpcomingItems,
  getOverdueItems,
  getItemsByDateRange
};

