import { useQuery } from '@tanstack/react-query';
import calendarDataService from '../services/calendarData';
import dayjs from 'dayjs';

export const useDayItems = (userId, date) => {
  const dateStr = date ? dayjs(date).format('YYYY-MM-DD') : dayjs().format('YYYY-MM-DD');
  
  return useQuery({
    queryKey: ['calendar', 'dayItems', userId, dateStr],
    queryFn: async () => {
      return await calendarDataService.getDayItems(userId, dateStr);
    },
    enabled: !!userId,
    staleTime: 1 * 60 * 1000, // 1 minute
  });
};

export const useUpcomingItems = (userId, days = 7) => {
  return useQuery({
    queryKey: ['calendar', 'upcoming', userId, days],
    queryFn: async () => {
      return await calendarDataService.getUpcomingItems(userId, days);
    },
    enabled: !!userId,
    staleTime: 2 * 60 * 1000, // 2 minutes
  });
};

export const useOverdueItems = (userId) => {
  return useQuery({
    queryKey: ['calendar', 'overdue', userId],
    queryFn: async () => {
      return await calendarDataService.getOverdueItems(userId);
    },
    enabled: !!userId,
    staleTime: 2 * 60 * 1000, // 2 minutes
  });
};

export const useMonthItems = (userId, startDate, endDate) => {
  const startStr = startDate ? dayjs(startDate).format('YYYY-MM-DD') : null;
  const endStr = endDate ? dayjs(endDate).format('YYYY-MM-DD') : null;
  
  return useQuery({
    queryKey: ['calendar', 'monthItems', userId, startStr, endStr],
    queryFn: async () => {
      return await calendarDataService.getItemsByDateRange(userId, startStr, endStr);
    },
    enabled: !!userId && !!startStr && !!endStr,
    staleTime: 5 * 60 * 1000, // 5 minutes
  });
};

