import { databases } from '../appwrite';
import { Query, ID } from 'appwrite';

const DATABASE_ID = process.env.REACT_APP_APPWRITE_DATABASE_ID || '69181c670024df0dd32b';
const REMINDERS_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_REMINDERS || '691f989400364a8670d8';

export async function createReminder(userId, reminder) {
  try {
    const { title, description, date, time, notification, priority, assignedTo = [], recurring = null } = reminder;
    
    // Handle date - could be ISO string or separate date/time
    let dateTime;
    if (typeof date === 'string' && date.includes('T')) {
      // Already an ISO string
      dateTime = new Date(date);
    } else if (time) {
      // Combine date and time
      const dateStr = typeof date === 'string' ? date : date.format('YYYY-MM-DD');
      const timeStr = typeof time === 'string' ? time : time.format('HH:mm');
      dateTime = new Date(`${dateStr}T${timeStr}`);
    } else {
      // Just date, use noon as default
      const dateStr = typeof date === 'string' ? date : date.format('YYYY-MM-DD');
      dateTime = new Date(`${dateStr}T12:00`);
    }
    
    // Validate date
    if (isNaN(dateTime.getTime())) {
      throw new Error('Invalid date or time value');
    }
    
    return await databases.createDocument(
      DATABASE_ID,
      REMINDERS_COLLECTION_ID,
      ID.unique(),
      {
        ownerId: userId,
        title: title.trim(),
        description: description || '',
        date: dateTime.toISOString(),
        notification: notification !== false, // Default to true
        priority: priority || 'medium',
        assignedTo: Array.isArray(assignedTo) ? assignedTo : [],
        recurring: recurring || null
      }
    );
  } catch (err) {
    console.error('Error creating reminder:', err);
    throw err;
  }
}

export async function listReminders(userId, date = null) {
  try {
    const queries = [Query.equal('ownerId', userId)];
    
    // Get all reminders for the user (we'll filter by date and recurring logic below)
    const response = await databases.listDocuments(
      DATABASE_ID,
      REMINDERS_COLLECTION_ID,
      queries
    );
    
    const allReminders = response.documents || [];
    
    if (!date) {
      return allReminders;
    }
    
    // Filter reminders that match the date
    const targetDate = new Date(date);
    const targetDay = targetDate.getDate();
    const targetMonth = targetDate.getMonth();
    const targetYear = targetDate.getFullYear();
    const targetDayOfWeek = targetDate.getDay();
    
    const matchingReminders = allReminders.filter(reminder => {
      const reminderDate = new Date(reminder.date);
      const reminderDay = reminderDate.getDate();
      const reminderMonth = reminderDate.getMonth();
      const reminderYear = reminderDate.getFullYear();
      const reminderDayOfWeek = reminderDate.getDay();
      
      // Exact date match
      if (reminderDay === targetDay && reminderMonth === targetMonth && reminderYear === targetYear) {
        return true;
      }
      
      // Check recurring patterns
      if (reminder.recurring) {
        switch (reminder.recurring) {
          case 'daily':
            // Show if reminder date is on or before target date
            return reminderDate <= targetDate;
          case 'weekly':
            // Show if same day of week and reminder date is on or before target date
            return reminderDayOfWeek === targetDayOfWeek && reminderDate <= targetDate;
          case 'monthly':
            // Show if same day of month and reminder date is on or before target date
            return reminderDay === targetDay && reminderDate <= targetDate;
          case 'yearly':
            // Show if same month and day, and reminder date is on or before target date
            return reminderDay === targetDay && reminderMonth === targetMonth && reminderDate <= targetDate;
          default:
            return false;
        }
      }
      
      return false;
    });
    
    return matchingReminders;
  } catch (err) {
    // If collection doesn't exist, return empty array
    if (err.code === 404) {
      console.warn('Reminders collection not set up yet');
      return [];
    }
    console.error('Error listing reminders:', err);
    return [];
  }
}

export async function updateReminder(documentId, reminder) {
  try {
    const { title, description, date, time, notification, priority, assignedTo, recurring } = reminder;
    
    // Handle date - could be ISO string or separate date/time
    let dateTime;
    if (typeof date === 'string' && date.includes('T')) {
      // Already an ISO string
      dateTime = new Date(date);
    } else if (time) {
      // Combine date and time
      const dateStr = typeof date === 'string' ? date : date.format('YYYY-MM-DD');
      const timeStr = typeof time === 'string' ? time : time.format('HH:mm');
      dateTime = new Date(`${dateStr}T${timeStr}`);
    } else {
      // Just date, use noon as default
      const dateStr = typeof date === 'string' ? date : date.format('YYYY-MM-DD');
      dateTime = new Date(`${dateStr}T12:00`);
    }
    
    // Validate date
    if (isNaN(dateTime.getTime())) {
      throw new Error('Invalid date or time value');
    }
    
    const updateData = {
      title: title.trim(),
      description: description || '',
      date: dateTime.toISOString(),
      notification: notification !== false,
      priority: priority || 'medium'
    };
    
    if (assignedTo !== undefined) {
      updateData.assignedTo = Array.isArray(assignedTo) ? assignedTo : [];
    }
    
    if (recurring !== undefined) {
      updateData.recurring = recurring || null;
    }
    
    return await databases.updateDocument(
      DATABASE_ID,
      REMINDERS_COLLECTION_ID,
      documentId,
      updateData
    );
  } catch (err) {
    console.error('Failed to update reminder:', err);
    throw err;
  }
}

export async function deleteReminder(documentId) {
  try {
    return await databases.deleteDocument(
      DATABASE_ID,
      REMINDERS_COLLECTION_ID,
      documentId
    );
  } catch (err) {
    console.warn('Failed to delete reminder');
    throw err;
  }
}

const remindersService = {
  createReminder,
  listReminders,
  updateReminder,
  deleteReminder,
};

export default remindersService;
